import React from 'react'
import Star from './Star'

import './Jumbotron.css'

class Jumbotron extends React.Component {
    constructor(props) {
        super(props);
        this.createSpaceStars = this.createSpaceStars.bind(this);
    }

    createSpaceStars() {
        var Stars = [];
        for (var i = 0; i < 300; i++) {
            Stars.push(<Star key={i}/>)
        }

        return Stars
    }

    render() {
        return (
            <div className="jumbotron">
                {/* {this.createSpaceStars()} */}
                <div className="row">
                    <div className="col-lg-12">
                        <h1>Robert Upchurch</h1>
                    </div>
                </div>

                <div className="row links">
                    {/*All icons are credited to flaticon.com*/}
                    <div className="col-sm-1 col-lg-2 col-lg-offset-1"><a target="__blank" href="https://www.linkedin.com/in/robert-upchurch/"><img alt="LinkedIn" src="data:image/svg+xml;utf8;base64," /></a></div>
                    <div className="col-sm-1 col-lg-2"><a target="__blank" href="https://github.com/RobertUpchurch"><img alt="Github" src="data:image/svg+xml;utf8;base64," /></a></div>
                    <div className="col-sm-1 col-lg-2"><a target="__blank" href="https://www.facebook.com/Robert.C.Upchurch?ref=bookmarks"><img alt="Facebook" src="data:image/svg+xml;utf8;base64," /></a></div>
                    <div className="col-sm-1 col-lg-2"><a target="__blank" href="https://www.instagram.com/robertupchurch/"><img alt="Instagram" src="data:image/svg+xml;utf8;base64," /></a></div>
                    <div className="col-sm-1 col-lg-2"><a target="__blank" href="https://www.youtube.com/channel/UCPB7T5SG7cmKWvVX6CyOJIA?view_as=subscriber"><img alt="Youtube" src="data:image/svg+xml;utf8;base64," /></a></div>
                </div>
            </div>
        )
    }
}

export default Jumbotron;